@import url(https://fonts.googleapis.com/css?family=Open+Sans);
::-moz-selection {
  background: #5a6f89;
  color: white;
}

::selection {
  background: #5a6f89;
  color: white;
}

* {
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  color: #5a6f89;
  outline: none;
}

body {
  margin: 0;
  padding: 0;
  background: white;
}

main {
  max-width: 1000px;
  width: 100%;
  margin: auto;
  padding: 10px;
}

.arrow-up {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 15;
  height: 40px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  cursor: pointer;
}

.arrow-up:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

button.search-btn {
  margin: 40px 0;
  min-width: 150px;
  height: 40px;
  background: #16a085;
  color: white;
  font-weight: bold;
  border: 1px solid transparent;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  cursor: pointer;
}

.search-btn:hover {
  background: #2abb9b;
}
header {
    width: 100%;
    height: 80px;
    box-shadow: 0 0 5px #bdc3c7;
    background: white;
}

header .inner-header {
    max-width: 1200px;
    width: 100%;
    height: 80px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.inner-header .header-heading {
    height: 70px;
    cursor: pointer;
}

.inner-header .header-logo {
    height: 80px;
    cursor: pointer;
    order: 3;
}

nav > ul {
    list-style-type: none;
}

nav > ul li {
    display: inline-block;
    margin: 0 10px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

nav > ul li a {
    text-transform: uppercase;
    text-decoration: none;
}

nav > ul li a:not(.active) {
    cursor: pointer;
}

nav > ul li a:not(.active):hover {
    color: #16a085;
}

nav > ul li a.active {
    border-bottom: 4px solid #16a085;
}


/* RESPONSIVE */
@media (max-width: 800px) {
    .inner-header {
        flex-direction: column;
        align-items: center;
    }

    .inner-header .header-heading {
        display: none
    }

    .inner-header .header-logo {
        height: 30px;
        margin-top: 5px;
        order: 1;
    }

    nav {
        order: 2;
    }

    nav ul {
        padding: 0;
        margin-top: 10px;
    }
}
.schedule {
    max-width: 1200px;
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;
}
.search-schedule-form {
    max-width: 400px;
    width: 100%;
    margin: 50px auto;
    text-align: left;
}
.search-schedule-form input, select {
    max-width: 380px;
    width: 100%;
    height: 40px;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid lightgray;
    color: #2e3131;
    background: white;
}

.search-schedule-form #date-label,
.search-schedule-form #country-label {
    font-size: 12px;
}

.search-schedule-form .search-btn {
    width: 150px;
    display: block;
    background: #16a085;
    color: white;
    font-weight: bold;
    border: 1px solid transparent;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    cursor: pointer;
    margin: 20px auto;
}

.search-schedule-form .search-btn:hover {
    background-color: #2abb9b;
}
.schedule-result {
    display: flex;
    margin: 10px 0;
    max-width: 900px;
    width: 100%;
}

.schedule-result:nth-child(odd) {
    background: #f6f6f5;
}

.schedule-result:nth-child(even) {
    background: #fbfbfb;
}

.schedule-result .result-info {
    padding: 15px;
    margin-left: 20px;
}

p.official-site {
    cursor: pointer;
    color: #16a085;
}


/* RESPONSIVE */
@media (max-width: 700px) {
    .schedule-result {
        flex-direction: column;
        align-items: center;
    }
    .schedule-result .result-info {
        margin-left: 0;
        max-width: 300px;
        overflow: hidden;
    }
}
.homepage-schedule {
    padding: 5px;
}

.homepage-schedule h1 {
    font-size: 30px;
}
.episode-info p img {
    height: 18px;
    vertical-align: -3px;
}

.episode-name {
    color: #3C948B; 
    font-weight: 'normal';
    cursor: pointer;
}

.episode-name:hover {
    opacity: .9;
}
.loader {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 200;
    left: 0;
    top: 0;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-ellipsis div {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #16a085;
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
            animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }
  .lds-ellipsis div:nth-child(1) {
    left: 8px;
    -webkit-animation: lds-ellipsis1 0.6s infinite;
            animation: lds-ellipsis1 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(2) {
    left: 8px;
    -webkit-animation: lds-ellipsis2 0.6s infinite;
            animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(3) {
    left: 32px;
    -webkit-animation: lds-ellipsis2 0.6s infinite;
            animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(4) {
    left: 56px;
    -webkit-animation: lds-ellipsis3 0.6s infinite;
            animation: lds-ellipsis3 0.6s infinite;
  }
  @-webkit-keyframes lds-ellipsis1 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes lds-ellipsis1 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @-webkit-keyframes lds-ellipsis3 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  @keyframes lds-ellipsis3 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  @-webkit-keyframes lds-ellipsis2 {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
    100% {
      -webkit-transform: translate(24px, 0);
              transform: translate(24px, 0);
    }
  }
  @keyframes lds-ellipsis2 {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
    100% {
      -webkit-transform: translate(24px, 0);
              transform: translate(24px, 0);
    }
  }
  
section.results {
    max-width: 1200px;
    width: 100%;
    margin:auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 5px;
}

button.back-to-results {
    margin: 60px 300px 20px;
    min-width: 150px;
    height: 40px;
    background: #16a085;
    color: white;
    font-weight: bold;
    border: 1px solid transparent;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    cursor: pointer;
}

div.no-matching-results {
    display: block;
    width: 100%;
    margin: auto;
    text-align: center;
    font-weight: bold;
}

div.no-cast {
    max-width: 1200px;
    width: 100%;
    text-align: center;
    margin: 50px auto;
    font-style: italic;
}

button.back-to-results:hover {
    background: #2abb9b;
}
.search-field {
    width: 800px;
    margin: 30px auto 70px;
    display: flex;
    justify-content: center;
}

.search-field input[type=text] {
    width: 350px;
    height: 40px;
    padding: 10px;
    color: #2e3131;
    border-radius: 0;
    border: 1px solid lightgray;
}

.search-field > .search-btn {
    width: 150px;
    background: #16a085;
    color: white;
    font-weight: bold;
    border: 1px solid transparent;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    cursor: pointer;
    margin: 0;
}

.search-field .search-btn:hover {
    background-color: #2abb9b;
}


/* RESPONSIVE */
@media (max-width: 700px) {
    .search-field {
        flex-direction: column;
        align-items: center;
    }

    .search-field input[type=text] {
        width: 250px;
        margin-bottom: 20px;
    }
}
.result {
    min-height: 295px;
    max-width: 800px;
    width: 100%;
    margin: 20px auto;
    display: flex;
    justify-content: flex-start;
    position: relative;
    font-size: 14px;
    background: white;
}

.result:nth-child(odd) {
    background: #f6f6f5;
}

.result:nth-child(even) {
    background: #fbfbfb;
}

.result-info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 30px;
}

.result-info h4 {
    text-transform: uppercase;
    font-size: 18px;
    margin-bottom: 5px;
}

.result-info p:first-of-type {
    margin-top: 30px;
}

.result-info p {
    margin: 5px 0;
}

.genres {
    display: flex;
    justify-content: flex-start;
}

.genre {
    font-size: 12px;
    background: lightgray;
    border-radius: 5px;
    margin: 0 10px 0 0;
    padding: 4px;
}

.btns {
    display: flex;
    justify-content: center;
}

.btns .search-btn {
    margin: 20px 10px 0 0;
    width: 150px;
    height: 40px;
    background: #16a085;
    color: white;
    font-weight: bold;
    border: 1px solid transparent;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    cursor: pointer;
}

.btns > .search-btn:hover {
    background: #2abb9b;
}


/* RESPONSIVE */
@media (max-width: 700px) {
    .btns {
        flex-direction: column;
    }
}

@media (max-width: 600px) {
    .result {
        flex-direction: column;
        align-items: center;
    }
}
.actor {
    min-height: 295px;
    width: 500px;
    margin: 15px 30px;
    display: flex;
    justify-content: flex-start;
    position: relative;
    padding: 10px;
    font-size: 14px;
    background: #f6f6f5;
}

.actor-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-left: 30px;
    height: 100%;
}

.actor-info-roles {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.actor-info img {
    max-width: 100px;
}

.actor-info h4 {
    text-transform: uppercase;
    font-size: 18px;
    margin-bottom: 25px;
}

.actor-info p {
    margin: 5px 0;
}

.actor-info ul {
    list-style-image: url('/images/television.png');
    padding: 0;
}

button.actor-roles-btn {
    margin: auto;
}


/* RESPONSIVE */
@media (max-width: 600px) {
    .actor {
        padding: 20px;
        flex-direction: column;
        width: auto;
    }

    .actor .search-btn {
        margin-left: auto;
        margin-right: auto;
    }

    .actor-info {
        margin-left: 0;
    }
}
.summary {
    display: flex;
    max-width: 900px;
    width: 100%;
    margin: 20px auto;
    font-size: 14px;
    align-items: flex-start;
    padding: 10px;
}

.summary img {
    min-width: 210px;
}

.summary-info {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    justify-content: space-between;
}

.summary-info p {
    margin: 0;
}

.summary .search-btn:first-of-type {
    margin-right: 10px;
}


/* RESPONSIVE */
@media (max-width: 700px) {
    .summary {
        flex-direction: column;
        align-items: center;
    }

    .summary img {
        margin-bottom: 20px;
    }

    .summary-btns {
        text-align: center;
        margin-top: 20px;
    }

    .summary-btns .search-btn {
        margin: 5px !important;
    }
}

@media (max-width: 349px) {
    .summary-btns {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
section.episodes {
    margin-top: 60px;
}

a {
    text-decoration: none;
}

button.episodes-back-btn {
    display: block;
    margin: auto;
}
.episode {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 20px;
    padding: 10px;
}

.episode:nth-of-type(odd) {
    background: #fbfbfb;
}

.episode:nth-of-type(even) {
    background: #f6f6f5;
}

.episode img {
    height: 140px;
}

.episode-info {
    height: 100%;
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.episode-info p {
    margin: 0;
    margin-bottom: 2px;
    font-size: 14px;
}

p.episode-name {
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 10px;
    text-align: center;
}


/* RESPONSIVE */
@media (max-width: 910px) {
    .episode {
        flex-direction: column;
        align-items: center;
    }

    .episode-info {
        margin-top: 10px;
        align-items: center;
    }
}
ul.pagination {
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 0;
    list-style: none;
    border-radius: 0.25rem;
}

ul.pagination li {
    margin: 5px;
    border-radius: 4px;
    color: #3C948B;
    border: none;
    background-color: #fff;
    display: block;
    height: 100%;
    padding: 5px 10px;
    line-height:  24px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

ul.pagination li.active {
    font-weight: bold;
}

ul.pagination li.disabled {
    display: none;
}

ul.pagination li:hover:not(.disabled) {
    font-weight: bold;
    cursor: pointer;
}
footer {
    width: 100%;
    height: 70px;
    margin: 80px auto 0;
    color: #6c7a89;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

footer .react-js {
    margin: 0 5px;
    font-style: italic;
    font-weight: bold;
}

footer .react-img {
    height: 30px;
    vertical-align: -40%;
    -webkit-animation: react-spin 2s linear 0s infinite;
    animation: react-spin 2s linear 0s infinite;
}

@-webkit-keyframes react-spin {
    0% {
      -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes react-spin {
    0% {
      -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

footer .heart-img {
    height: 25px;
    vertical-align: -40%;
    margin: 5px 5px;
    -webkit-animation: heart-beat 1s linear 0s infinite;
    animation: heart-beat 1s linear 0s infinite;
}

@-webkit-keyframes heart-beat {
    20% {
        -webkit-transform: scale(1.2);
        transform: scale(1.2)
    }
}

@keyframes heart-beat {
    20% {
        -webkit-transform: scale(1.2);
        transform: scale(1.2)
    }
}
